import React from 'react';

import {
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  makeStyles,
} from '@material-ui/core';
import { DragIndicator } from '@material-ui/icons';
import clsx from 'clsx';
import {
  DragDropContext,
  Droppable,
  Draggable,
  DropResult,
  ResponderProvided,
} from 'react-beautiful-dnd';
import { useSelector } from 'react-redux';
import { getUserTransliterators, User } from 'src/lib/api';
import { AppState } from 'src/state';

const useStyles = makeStyles((theme) => ({
  root: {},
  subList: {
    '&.dragging-over': {
      backgroundColor: 'lightblue',
    },
  },
  subHeader: {
    textAlign: 'left',
    lineHeight: '1.5em',
  },
  subListItem: {
    '&.is-dragging': {
      backgroundColor: 'red',
    },
  },
  subListItemText: {
    fontSize: '0.8em',
  },
}));

type RankTransliteratorListProps = {
  user: User;
  className?: string;
};

const TransliteratorLabel = {
  active: 'Active',
  inactive: 'InActive',
};

// const TRANSLITERATOR_DRAG_DROP_ID = {
//   DROP: 'TRANSLITERATOR_DROP',
// };
export const RankTransliteratorList: React.FC<RankTransliteratorListProps> = (
  props: RankTransliteratorListProps
) => {
  const classes = useStyles();
  const transliteratorsMap = useSelector(
    (st: AppState) => st.kuralIndexMap.transliterator
  );
  const userTransliterators = getUserTransliterators(
    props.user,
    transliteratorsMap
  );
  const onDragEnd = (result: DropResult, provided: ResponderProvided): void => {
    console.log(result, provided);
  };
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      {Object.entries(userTransliterators).map(([key, list]) => (
        <Droppable droppableId={key} key={key}>
          {(provided, snapshot) => (
            <List
              {...provided.droppableProps}
              ref={provided.innerRef}
              className={clsx(
                classes.subList,
                snapshot.isDraggingOver && 'dragging-over'
              )}
              subheader={
                <ListSubheader component='div' className={classes.subHeader}>
                  {TransliteratorLabel[key as 'active' | 'inactive']}
                </ListSubheader>
              }>
              {list.map((tItem, index) => (
                <Draggable
                  key={tItem.id}
                  draggableId={tItem.id.toString()}
                  index={tItem.id}>
                  {(provided, snapshot) => (
                    <ListItem
                      className={clsx(
                        classes.subListItem,
                        snapshot.isDragging && 'is-dragging'
                      )}
                      style={provided.draggableProps.style}
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}>
                      <ListItemIcon>
                        <DragIndicator />
                      </ListItemIcon>
                      <ListItemText
                        disableTypography
                        primary={tItem.name}
                        className={classes.subListItemText}
                      />
                    </ListItem>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </List>
          )}
        </Droppable>
      ))}
    </DragDropContext>
  );
};
